import React from "react";
import Certainty from "../Certainty/Certainty";
import Nodata from "Components/Nodata/Nodata";
import DataPointsUsecase from "../DataPointsUsecase/DataPointsUsecase";
import DataUsable from "../DataUsable/DataUsable";
import Pricing from "../Pricing/Pricing";
import ThreePoints from "../ThreePoints/ThreePoints";

import PageData from "../../Data/pages";
import { useLocation } from "react-router-dom";


const Usecases = () => {
	const location = useLocation();
	const pathname = location.pathname;
	const pageData = PageData[pathname];
	
  return (
		<div className=" px-0">
			<Certainty />

			{window.innerWidth > 768 && <Nodata />}

			<DataPointsUsecase 
				content={pageData.intelData}
			/>

			{window.innerWidth > 768 && (
				<>
					<DataUsable />
					<ThreePoints />
				</>
			)}

			<Pricing />
		</div>
  );
};

export default Usecases;
